import { title } from 'process';
import { TasksService } from './../../services/tasks.service';
import { ActivatedRoute } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { Title } from '@angular/platform-browser';

@Component({
  selector: 'app-task',
  templateUrl: './task.component.html',
  styleUrls: ['./task.component.css']
})
export class TaskComponent {
  Id = this.rouit.snapshot.paramMap.get("id")
  Ix = this.task.x[this.Id].des
  object = this.task.x[this.Id]

  constructor(private rouit : ActivatedRoute, private task : TasksService, private title : Title) {
    this.title.setTitle("Edit Task")
  }

   addTask() {
    this.task.editeTask(this.Id, this.object);
    this.task.saveall();
   }
   delete(Id){
    this.task.x.splice(Id, 1);
    this.task.saveall();
   }
}
